.canhcam-breadcrumb-1 {
	background: #f5f5f5;
	.breadcrumbs-list {
		.breadcrumb {
			padding-left: 0;
			margin-bottom: 0;
			list-style-type: none;
			@include pad(.8rem null);
			background: none;
			li {
				@include pad(null 20px);
				a , span {
					@include fz(r(13.83px));
					@include fw(500);
					text-decoration: none;
					color: #bababa; }
				&:first-child {
					padding-left: 0; }
				&:last-child {
					padding-right: 0; }
				+ li {
					position: relative;
					&:before {
						content: '|';
						@include pad(0);
						@include pos(abs , 0 null null 0);
						color: #d7d7d7; } } } } } }
