.canhcam-home-7 {
	@include pad(2rem null);
	.homes-list {
		.item {
			text-decoration: none;
			display: block;
			figure {
				.boxzoom {
					img {
						width: 100%; } }
				figcaption {
					@include pad(1rem);
					background: #f4f4f4;
					time {
						display: block;
						@include fz(r(14.82px));
						@include lh(r(14.82px));
						color: #999999;
						margin-bottom: r(14px); }
					h4 {
						color: #333333;
						@include fz(r(16px));
						margin-bottom: 0;
						@include fw(500);
						@include text-clamp(2 , 2.5rem); } } } } }
	@include media-breakpoint-up(lg) {
		@include pad(4rem null);
		.homes-list {
			.item {
				figure {
					margin-bottom: 0;
					.boxzoom {
						overflow: hidden;
						img {
							@include transition(all .3s ease-in-out); } }
					figcaption {
						@include pad(r(28px));
						padding-bottom: r(45px); } }
				&:hover {
					.boxzoom {
						img {
							@include transform(scale(1.2)); } } } } } } }
