.canhcam-home-5 {
	@include pad(2rem null);
	.homes-list {
		.products-list {
			overflow: hidden;
			.owl-stage-outer {
				overflow: visible; }
			.owl-nav {
				margin-top: 0;
				button {
					@include mar(0);
					&.owl-prev , &.owl-next {
						@include fz(r(30px));
						color: #999999;
						@include pos(abs , 50% null null null);
						@include transform(translateY(-50%));
						&:focus {
							outline: none; }
						&:hover {
							background: none; } }
					&.owl-prev {
						left: 0; }
					&.owl-next {
						right: 0; } } } } }
	@include media-breakpoint-up(lg) {
		@include pad(4rem null);
		.homes-list {
			.products-list {
				.owl-stage-outer {
					padding-top: .5rem; }
				.owl-nav {
					button {
						&.owl-prev , &.owl-next {
							top: 35%; } } } } } } }
