@function r($size) {
	@return ($size/16px)*1rem; }


@function mbTitle($size) {
	$ratio: 48px / 42px;
	@return $size / $ratio; }

@mixin line($line) {
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-line-clamp: $line;
	overflow: hidden;
	-webkit-box-orient: vertical; }

@mixin mdi($content) {
	display: inline-block;
	font-family: "Material Design Icons";
	font-size: inherit;
	text-rendering: auto;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	line-height: inherit;
	-webkit-font-smoothing: antialiased;
	content: $content; }

@mixin linearicons($content) {
	font-family: 'Linearicons-Free';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	content: $content; }
@mixin text-gradient() {
	background: rgb(133,234,92);
	background: -moz-linear-gradient(to right, rgba(133,234,92,1) 0%, rgba(41,138,196,1) 50%, rgba(133,234,92,1) 100%);
	background: -webkit-linear-gradient(to right, rgba(133,234,92,1) 0%, rgba(41,138,196,1) 50%, rgba(133,234,92,1) 100%);
	background: linear-gradient(to right, rgba(133,234,92,1) 0%, rgba(41,138,196,1) 50%, rgba(133,234,92,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#85ea5c",endColorstr="#85ea5c",GradientType=1); }


@mixin fas($content) {
	font-family: 'Font Awesome 5 Free';
	display: inline-block;
	font-style: normal;
	font-variant: normal;
	text-rendering: auto;
	line-height: 1;
	content: $content; }
@mixin text-clamp($lines, $line-height: false) {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $lines;

	// Fallback for non-Webkit browsers
	// (won't show `…` at the end of the block)
	@if $line-height {
		max-height: $line-height * $lines; } }

