.canhcam-home-6 {
	@include pad(2rem null);
	background-attachment: fixed;
	.homes-list {
		.list-promos {
			.item {
				display: block;
				text-decoration: none;
				figure {
					margin-bottom: 0;
					text-align: center;
					.boxzoom {
						// img
 }						// 	width: 100%
					figcaption {
						@include pad(1rem);
						padding-bottom: 0;
						h3 {
							@include fz(r(14.82px));
							color: #ffffff;
							margin-bottom: auto; }
						.price {
							margin-top: r(22px);
							span {
								display: block;
								&.new {
									@include fz(r(17.78px));
									@include lh(r(17.78px));
									@include fw(700);
									color: #fff200;
									margin-bottom: r(10px); }
								&.old {
									@include fz(r(14.82px));
									@include lh(r(14.82px));
									@include fw(400);
									color: #ffffff;
									text-decoration: line-through; } } } } } }
			.owl-nav {
				margin-top: 0;
				button {
					@include mar(0);
					&.owl-prev , &.owl-next {
						@include fz(r(30px));
						color: #999999;
						@include pos(abs , 35% null null null);
						@include transform(translateY(-50%));
						&:focus {
							outline: none;
							background: none; }
						&:hover {
							background: none; } }
					&.owl-prev {
						left: 0; }
					&.owl-next {
						right: 0; } } } } }
	@include media-breakpoint-up(lg) {
		@include pad(4rem null);
		.homes-list {
			.list-promos {
				.item {
					figure {
						figcaption {
							padding-top: r(28px); } } } } } } }
