.smx-product-2 {
	padding: 2rem 0;


	.sort_filter_mb {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 1rem;
		@media (min-width: 768px) {
			display: none; }
		.btn-trigger-filter {
			@media (min-width: 768px) {
				display: none; } }
		#sortbox {
			position: relative;
			@media (min-width: 768px) {
				display: none; }
			.selectbox {
				width: r(125px);
				overflow: hidden; }
			&:after {
				content: '\F35D';
				font-family: "Material Design Icons";
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 0;
				color: #298ac4;
				font-size: 1rem;
				pointer-events: none; }
			select {
				width: 100%;
				border: 0;
				font-size: r(14.82px);
				color: #298ac4;
				-webkit-appearance: none;
				background-color: transparent; } } }
	.categories-wrapper,.filter-wrapper {
		h2 {
			font-size: r(15.76px);
			padding-bottom: 0.5rem;
			text-transform: uppercase;
			margin-bottom: 0.5rem;
			background: linear-gradient(to right,#298ac4 0,#85ea5c 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			display: flex;
			justify-content: space-between;
			&:after {
				width: 100%; }
			&:before {
				@include mdi('\F415');
				-webkit-text-fill-color: initial;
				position: absolute;
				right: -6px;
				background: linear-gradient(to right,#298ac4 0,#85ea5c 100%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent; }
			&.active {
				&:before {
					@include mdi('\F374'); } } } }
	.hassub {
		margin-bottom: 0.8rem;
		cursor: pointer;
		a {
			display: flex;
			width: 100%;
			font-size: r(14.82px);
			text-transform: uppercase;
			font-weight: 400;
			color: #333333;
			align-items: center;
			justify-content: space-between;
			&.active {
				font-weight: 600; }
			.mdi-menu-down {
				&.active {
					transform: rotate(180deg);
					color: #298ac4; } } }
		.hassub-menu {
			padding-left: r(20px);
			display: none;
			list-style: none;
			li {
				padding: r(10px) 0 0 0;
				a {
					font-size: r(13.83px);
					color: #999999;
					font-weight: 400;
					text-transform: none; } }
			&.active {
				display: block; } } }

	.categories-wrapper {
		margin-bottom: 1.2rem;
		em {
			font-size: 1.2rem; }
		.hassub {
			.sub-title {
				text-transform: uppercase;
				&.active {
					color: #333333;
					font-weight: 600; } }
			.hassub-menu {
				li {
					&.active {
						a {
							color: #298ac4;
							font-weight: 600; } } } } } }

	.filter-wrapper {
		.filter-item {
			.hassub-menu {
				li {
					a {
						justify-content: flex-start;
						color: #333333;
						position: relative;
						&:before {
							content: '';
							width: 15px;
							height: 15px;
							background: #fff;
							box-shadow: 0px 0px 0px 1px #e1e1e1;
							margin-right: 1rem; }
						&:after {
							@include mdi('\F12C');
							position: absolute;
							font-size: r(19.76px);
							top: -8px;
							color: #298ac4;
							display: none; } }
					&.active {
						a {
							&:after {
								display: block; } } } } } } }
	.products-list {
		@media (min-width: 350px) {
			.col-sm-6 {
				-ms-flex: 0 0 50%;
				flex: 0 0 50%;
				max-width: 50%; } }
		@media (min-width: 992px) {
			.col-lg-4 {
				-ms-flex: 0 0 33.333333%;
				flex: 0 0 33.333333%;
				max-width: 33.333333%; } }
		#title {
			width: 100%;
			text-align: center;
			margin-bottom: 1rem;
			color: #298ac4;
			text-transform: uppercase;
			@media (min-width: 768px) {
				display: none; } }
		figure {
			margin-bottom: 2rem;
			.boxzoom {
				img {
					width: 100%; } } }
		.product__item {
			text-decoration: none; }
		@include media-breakpoint-up(lg) {
			figure {
				margin-bottom: 0; }
			.product__item {
				margin-bottom: r(60px); } } }
	.sort-bar {
		width: 100%;
		display: flex;
		position: relative;
		justify-content: space-between;
		align-items: center;
		background: #fff;
		border-radius: 5px;
		@include box-sha(0 0 5px 1px rgba(0,0,0,.1));
		padding: 0.6rem 1rem;
		margin-bottom: r(40px);
		@media (max-width: 768px) {
			display: none; }
		h3 {
			font-size: r(19.76px);
			color: #666666; }
		.selectbox {
			margin-right: 1.2rem;
			width: r(135px);
			position: relative;
			overflow: hidden;
			&:after {
				content: '\F35D';
				font-family: "Material Design Icons";
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 0;
				color: #298ac4;
				font-size: 1rem;
				pointer-events: none; }
			select {
				width: 100%;
				border: 0;
				font-size: r(14.82px);
				color: #298ac4;
				-webkit-appearance: none;
				background-color: transparent; } } } }
