.canhcam-footer-1 {
	padding: 2rem 0;
	.footer-link {
		h2 {
			font-size: 1rem;
			text-transform: uppercase;
			font-weight: 500;
			color: #666666;
			position: relative;
			padding-bottom: 0.75rem;
			margin-bottom: 0.8rem;
			@media (min-width: 576px) {
				margin-bottom: r(36px); } }
		ul {
			li {
				display: flex;
				margin-bottom: 0.5rem;
				align-items: baseline;
				a {
					font-size: r(13px);
					color: #666666; }
				em {
					margin-right: 0.3rem;
					color: #666666; } } }
		.certification,.social-list {
			ul {
				li {
					display: inline-block; } } }
		.certification {
			margin-bottom: r(16px);
			h2 {
				@media (min-width: 576px) {
					margin-bottom: r(20px); } } }
		.social-list {
			h2 {
				@media (min-width: 576px) {
					margin-bottom: r(18px); } }
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				background: #298ac4;
				width: r(36px);
				height: r(36px);
				border-radius: 50%;
				em {
					margin-right: 0;
					font-size: 1.1rem;
					color: color-get($mau,trang); } } } } }
