
.canhcam-breadcrumb-2 {
	@include pad(r(72px) 0 r(27px));
	.bullets {
		display: flex;
		margin: 0;
		padding: 0;
		overflow: hidden;
		background: darken(color-get($mau,trang),5%);
		@include bor-rad(5px);
		@include pad(0.5rem 1rem);
		.bullet-wrapper {
			list-style-type: none;
			position: relative;
			display: flex;
			flex-basis: 0;
			flex-grow: 1;
			flex-direction: column;
			text-align: center;
			align-items: center;
			@include fz(.85rem);
			.num {
				background: darken(color-get($mau,trang),10%);
				width: 2rem;
				height: 2rem;
				@include bor-rad(50%);
				color: #333333;
				display: flex;
				align-items: center;
				justify-content: center;
				@include fw(500); }
			.title {
				display: block;
				font-size: r(12.84px);
				@include fw(500); }
			&.active {
				.num {
					background: linear-gradient( to right, rgb(41,138,196) 0%, rgb(133,234,92) 100%);
					color: color-get($mau,trang); } } }
		@include media-breakpoint-up(sm) {
			.bullet-wrapper {
				@include fz(1rem);
				&:after {
					content: '';
					position: absolute;
					overflow: hidden;
					display: inline-block;
					width: 6rem;
					height: 6rem;
					top: 50%;
					right: 1rem;
					border-top: 1px solid darken(color-get($mau,trang),15%);
					border-right: 1px solid darken(color-get($mau,trang),15%);
					transform: rotate(55deg) skew(22deg) translateY(-3rem); }
				&:last-child {
					&:after {
						content: none; } } } }
		@include media-breakpoint-up(lg) {
			.bullet-wrapper {
				flex-direction: initial;
				margin-right: 2rem;
				.title {
					margin-left: 1rem; }
				&:last-child {
					margin-right: 0; } } } } }
