

p ,ul ,h1,h2,h3,h4,h5,h6,figure,div, * {
	margin: 0;
	padding: 0;
	transition: all .3s ease-in-out; }
html, body {
	font-size: 16px;
	line-height: 1.5;
	font-weight: 400;
	font-family: 'Montserrat', sans-serif; }
main {
	overflow-x: hidden; }
input,select {
	outline: none; }
a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }
.title-line {
	position: relative;
	&:after {
		content: '';
		position: absolute;
		height: 2px;
		width: 70px;
		background: linear-gradient( to right, rgb(41,138,196) 0%, rgb(133,234,92) 100%);
		left: 0;
		bottom: 0; } }


.mx__title {
	@include fz(r(30px));
	// +lh(r(30px))
	@include lh(r(44px));
	text-align: center;
	text-transform: uppercase;
	margin-bottom: r(30px);
	position: relative;
	color: #333333;
	&:before {
		content: '';
		display: block;
		width: r(120px);
		height: r(2px);
		@include pos(abs , null null r(-15px) 50%);
		@include transform(translateX(-50%)); }
	&--white {
		color: #ffffff;
		&:before {
			background: #ffffff; } }
	&--gradient {
		@include text-gradient();
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		&:before {
			@include text-gradient(); } }
	@include media-breakpoint-up(lg) {
		@include fz(r(36px));
		// +lh(r(36px))
		// +lh(44p)
		margin-bottom: r(45px);
		&:before {
 } } }			// bottom: r(-20px)
.product__item {
	figure {
		margin-bottom: 1.5rem;
		.boxzoom {
			@include box-sha(0 0 18px 3px rgba(0,0,0,.1));
			position: relative;
			overflow: hidden;
			img {
				width: 100%; }
			.boxdetail {
				@include pos(abs, 0 null null 0);
				width: 101%;
				height: 101%;
				background: linear-gradient( to right, rgba(41,138,196,.85) 0%, rgba(133,234,92,.85) 100%);
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				visibility: hidden;
				opacity: 0;
				@include transition(all .3s ease-in-out);
				a {
					text-decoration: none;
					color: #ffffff;
					@include fz(13.83px);
					@include fw(500);
					@include pad(.5rem);
					width: r(178px);
					@include bor-rad(5px);
					display: flex;
					align-items: center;
					justify-content: center;
					@include box-sha(0 0 0 1px #ffffff);
					margin-bottom: r(10px);
					// white-space: nowrap
					.mdi {
						@include fz(r(20px));
						@include lh(r(20px));
						margin-right: r(10px); }
					&:last-child {
						margin-bottom: 0; }
					@media (max-width: 576px) {
						width: 90%;
						text-align: center;
						.mdi {
							display: none; } }
					@media (max-width: 350px) {
						.mdi {
							display: block; } } } }
			&:hover {
				.boxdetail {
					opacity: 1;
					visibility: visible; } } }
		figcaption {
			padding-top: r(20px);
			text-align: center;
			display: flex;
			flex-direction: column;
			ul {
				display: flex;
				justify-content: center;
				align-items: center;
				list-style-type: none;
				padding-left: 0;
				margin-bottom: 1rem;
				li {
					margin-right: 1.3rem;
					span {
						display: block;
						width: r(11px);
						height: r(11px);
						@include bor-rad(50%); }
					&:last-child {
						margin-right: 0; } } }
			.name {
				margin-bottom: auto;
				a {
					text-decoration: none;
					@include fz(r(14px));
					@include fw(400);
					color: #333333;
					display: block; } }
			span {
				color: #298ac4;
				@include fw(700);
				@include fz(r(17px));
				display: block;
				margin-top: .5rem; } } }
	@include media-breakpoint-up(lg) {
		figure {
			margin-bottom: 0;
			.boxzoom {
				overflow: hidden;
				img {
					@include transition(all .3s ease-in-out); } }
			figcaption {
				ul {
					li {
						margin-right: r(7px); } } }
			&:hover {
				.boxzoom {
					img {
						@include transform(scale(1.1)); } } } } } }

.mx-btn {
	text-align: center;
	a {
		text-decoration: none;
		text-transform: uppercase;
		color: #ffffff;
		@include fw(400);
		@include fz(r(15.8px));
		@include lh(r(15.8px));
		display: inline-block;
		@include pad(r(16px) r(40px));
		@include bor-rad(10px);
		background: linear-gradient( to right, rgb(41,138,196) 0%, rgb(133,234,92) 100%);
		&:after {
			content: '\f045';
			font-family: 'Material Design Icons';
			@include fz(r(19.76px));
			@include lh(r(19.76px));
			margin-left: r(20px); } } }
