	.swiper-container {
		height: r(300px);
		.swiper-slide {
			overflow: hidden;
			.slide-inner {
				@include pos(abs , 0 null null 0);
				width: 100%;
				height: 100%;
				display: flex;
				align-items: center;
				text-decoration: none;
				h2 {
					@include fz(1.1rem);
					@include lh(25px);
					@include fw(200);
					margin-bottom: 2.14rem;
					color: $den-1; }
				.see__more {
					display: inline-block;
					p {
						@include fz(r(14px));
						@include fw(400);
						color: $den-1;
						@include pad(.5rem 2.5rem);
						@include bor-rad(5px);
						@include box-sha(inset 0 0 0 1px #000000);
						display: inline-block;
						margin-bottom: 0;
						display: flex;
						align-items: center;
						&:after {
							content: '\e87a';
							font-family: 'Linearicons-Free';
							@include fz(r(18px));
							display: inline-block;
							margin-left: .5rem; } } } } } }
	.swiper-btn {
		width: 2.14rem;
		height: 2.14rem;
		background-size: cover;
		&.swiper-button-next {
			background-image: url('./../img/home/swiper_next.png');
			right: 0; }
		&.swiper-button-prev {
			background-image: url('./../img/home/swiper_prev.png');
			left: 0; } }
	.swiper-pagination {
		bottom: r(50px);
		.swiper-pagination-bullet {
			width: r(40px);
			height: r(6px);
			@include bor-rad(3px);
			opacity: 1;
			background: $xam-1;
			&.swiper-pagination-bullet-active {
				@include background-gradient($startColor: rgb(8,174,234) , $endColor:  rgb(42,245,152)); }
			&:focus {
				outline: none; } } }
	@include media-breakpoint-up(sm) {
		.swiper-container {
			height: r(480px); } }
	@include media-breakpoint-up(lg) {
		.swiper-container {
			height: r(577px);
			.swiper-slide {
				.slide-inner {
					h2 {
						@include fz(r(30px));
						@include lh(r(48px)); } } } }
		.swiper-btn {
			width: 4rem;
			height: 4rem;
			&.swiper-button-next {
				right: 8rem; }
			&.swiper-button-prev {
				left: 8rem; } }
