.canhcam-checkout-1 {
	padding-bottom: 3.5rem;
	.table td, .table th {
		vertical-align: middle; }
	h3 {
		@include pad(.75rem 0);
		margin-bottom: 0;
		font-size: r(14.82px);
		text-transform: uppercase;
		span {
			font-weight: 600; }
		i {
			margin-right: 0.5rem;
			color: #298ac4; } }
	table {
		thead {
			background: #f2f2f2;
			.product-details {}
			.product-price {}
			.product-count {}
			.product-result {
				text-align: right; }
			tr {
				th {
					border-bottom: 0;
					border-left: 0;
					border-right: 0;
					font-weight: 500;
					font-size: r(14.82px); } } }
		tbody {
			td {
				border-left: 0;
				border-right: 0; }
			.product-details {
				.media {
					align-items: center;
					img {
						max-width: 80px; }
					.media-body {
						@include fz(.9rem);
						h4 {
							@include fz(.9rem);
							@include fw(bold);
							a {
								color: darken(color-get($mau,trang),80%); } } } } }
			.product-price {
				.price {}
				.promotion {
					color: red;
					line-height: 1rem;
					font-size: .9rem; } }
			.product-count {
				.form-group {
					margin-bottom: 0; }
				#quantity {
					input {
						max-width: 40px;
						text-align: center;
						border: 0;
						max-height: 31px;
						background: #f4f4f4; }
					.btn {
						max-height: 31px;
						border: 0;
						display: flex;
						justify-content: center;
						align-items: center;
						line-height: 0;
						background: #f4f4f4; }
					.bootstrap-touchspin-up {
						border-radius: r(14.5px);
						border-top-left-radius: 0;
						border-bottom-left-radius: 0; }
					.bootstrap-touchspin-down {
						border-radius: r(14.5px);
						border-top-right-radius: 0;
						border-bottom-right-radius: 0; } } }
			.product-result {
				text-align: right;
				white-space: nowrap;
				.sum {
					color: #f30a0a;
					font-size: r(14.82px);
					@include lh(1.5rem);
					@include fw(500); } } } }
	.total {
		padding-bottom: 2rem;
		font-size: r(14.82px);
		font-weight: 500;
		.price {
			span {
				color: #ff0000;
				font-size: r(19.76px); } }
		.price-vat {
			font-size: r(11.85px); } }
	.navi {
		.btn-prev,.btn-next {
			padding: 0.375rem 1.5rem;
			font-size: r(13.83px); }
		.btn-prev {
			background: #ededed;
			color: #333; }
		.btn-next {
			background: #298ac4;
			color: #fff;
			font-weight: 500; } }
	.product-viewed-wrapper {
		padding: 0.875rem 0.5rem;
		border: 1px solid #acacac;
		cursor: grabbing;
		.product-viewed {
			.owl-item {
				display: flex;
				justify-content: center; }
			.item {
				display: flex;
				flex-direction: column;
				@media (min-width: 992px) {
					width: 136px; }
				.media {
					flex-direction: column;
					justify-content: center;
					align-items: center;
					img {
						max-width: 120px; }
					.media-body {
						text-align: center;
						color: #333;
						font-size: r(11.85px);
						.brand {}
						.name {
							text-transform: uppercase; } } }
				.btn {
					font-size: r(12px);
					margin-top: 0.5rem;
					border-color: #acacac;
					color: #333;
					display: flex;
					align-items: center;
					justify-content: center;
					&:hover {
						background: transparent; } } } } }

	// Mobile
	@include media-breakpoint-down(sm) {
		table {
			thead {
				display: none; }
			tbody {
				tr {
					border-bottom: 1px solid darken(color-get($mau,trang),10%);
					.product-details {
						float: left;
						border: 0;
						width: 50%; }
					.product-price,
					.product-count,
					.product-result {
						width: 50%;
						float: left;
						border: 0;
						text-align: right; }
					.product-result {
						.sum {
							@include fz(1rem);
							@include lh(1rem); } }
					.product-count {
						padding-top: 0;
						padding-bottom: 0;
						.form-inline {
							justify-content: flex-end; } } } } } }
	@include media-breakpoint-down(xs) {
		table {
			tbody {
				tr {
					.product-details {
						width: 100%; }
					.product-price,
					.product-count,
					.product-result {
						width: 100%; } } } } } }



