.canhcam-sale-1 {
	@include pad(2rem null);
	.sales-list {
		.product__item {
			figure {
				margin-bottom: 2rem;
				figcaption {
					.cost {
						span {
							&.old {
								color: #999999;
								@include fz(r(13.83px));
								@include fw(400);
								margin-top: 0;
								text-decoration: line-through; } } } } } } }
	@include media-breakpoint-up(lg) {
		@include pad(4rem null);
		.sales-list {
			.product__item {
				figure {
					margin-bottom: r(40px); } } } } }
